<template>
  <el-dialog
    width="33%"
    :title="textMap[formType]"
    :visible="formVisible"
    :before-close="exitForm"
    :destroy-on-close="true"
    :close-on-click-modal="false"
  >
    <el-form
      ref="dataForm"
      v-loading="loading"
      :rules="rules"
      :model="dataForm"
      label-position="top"
    >
      <el-form-item label="Last name" prop="last_name">
        <el-input v-model="dataForm.last_name" />
      </el-form-item>
      <el-form-item label="First name" prop="first_name">
        <el-input v-model="dataForm.first_name" />
      </el-form-item>
      <el-form-item label="Middle name" prop="middle_name">
        <el-input v-model="dataForm.middle_name" />
      </el-form-item>
      <el-form-item label="Email" prop="email">
        <el-input v-model="dataForm.email" />
      </el-form-item>
      <el-form-item v-if="showField" label="Password" prop="password">
        <el-input
          ref="password"
          v-model="dataForm.password"
          :type="passwordType"
          placeholder="Password"
          name="password"
        />
      </el-form-item>

      <el-form-item v-if="showField" label="Password confirmation" prop="password_confirmation">
        <el-input
          v-model="dataForm.password_confirmation"
          :type="passwordType"
          placeholder="Confirm password"
          name="password_confirmation"
        />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="formVisible = false">CANCEL</el-button>
      <el-button size="medium" @click="onSubmit">SAVE</el-button>
    </span>
  </el-dialog>

</template>

<script>
import {
  unifiedMutation
} from '@/graphql/unified'
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    return {
      loading: true,
      titleForm: 'User',
      textMap: {
        add: '',
        edit: ''
      },
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        module: '',
        data: '',
        user_registration_type: '',
        request: '',
      },
      dataForm: {
        id: undefined,
        last_name: '',
        first_name: '',
        middle_name: '',
        email: '',
        password: '',
        password_confirmation: '',
        created_by: '',
        updated_by: ''
      },
      passwordType: 'password',
      rules: {
        last_name: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        first_name: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        middle_name: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        email: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        password: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        password_confirmation: [{ required: true, message: 'This is required.', trigger: 'blur' }],
      },
      showField: true
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New ${this.titleForm}`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    handleCreate() {
      console.log('handleUpdate')
      this.loading = false
      this.showField = true
      this.resetModel()
    },
    handleUpdate(row) {
      this.showField = false
      console.log('handleUpdate', row)
      this.loading = false
      this.dataForm = row
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    create() {
      this.query.module = this.$route.name
      this.query.data = this.dataForm
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.request = 'create'
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          if (this.dataForm.password === this.dataForm.password_confirmation) {
            unifiedMutation(this.query, (response, success) => {
              if (success) {
                this.exitForm()
                this.successForm()
                this.$message({
                  message: response.unifiedMutation.message,
                  dangerouslyUseHTMLString: true,
                  type: 'success'
                })
              } else {
                this.tableLoading = false
                this.$message({
                  message: response.message,
                  dangerouslyUseHTMLString: true,
                  type: 'error'
                })
              }
            })
          } else {
            this.$message({
              message: 'password did not matched',
              dangerouslyUseHTMLString: true,
              type: 'error'
            })
          }
        }
      })
    },
    update() {
      this.query.module = this.$route.name
      this.query.data = this.dataForm
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.request = 'update'
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          unifiedMutation(this.query, (response, success) => {
            if (success) {
              this.exitForm()
              this.successForm()
              this.$message({
                message: response.unifiedMutation.message,
                dangerouslyUseHTMLString: true,
                type: 'success'
              })
            } else {
              this.tableLoading = false
              this.$message({
                message: response.message,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
        }
      })
    },
    resetModel() {
      this.dataForm = {
        id: undefined,
        last_name: '',
        first_name: '',
        middle_name: '',
        email: '',
        created_by: '',
        updated_by: ''
      }
    },
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

<style lang="scss" scoped>
.cancel {
  background-color: $cancel;
  color: gray;

}
</style>
