import { apolloClientInstance } from '@/vue-apollo'
import UnifiedQuery from './UnifiedQuery.graphql'
import UnifiedMutation from './UnifiedMutation.graphql'

export function unifiedQuery(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.query({
      query: UnifiedQuery,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}

export function unifiedMutation(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.mutate({
      mutation: UnifiedMutation,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}
